import './styles.css';
import { debounce as _debounce } from 'lodash';
import countiresApi from './js/api/fetchCountries';
import countriesListTemplate from '../src/templates/countries-list.hbs';
import countryCardTemplate from '../src/templates/country.hbs';
import { error } from '@pnotify/core';
import '@pnotify/core/dist/PNotify.css';
import '@pnotify/core/dist/BrightTheme.css';

const inputRef = document.querySelector('.input');
const countriesListRef = document.querySelector('.wrapper');

inputRef.addEventListener(
  'input',
  _.debounce(() => handleInput(), 500),
);

function handleInput() {
  if (!inputRef.value) {
    countriesListRef.innerHTML = '';
    return;
  }
  countiresApi
    .fetchCountries(inputRef.value)
    .then(results => handleResults(results));
}

function handleResults(results) {
  if (results.status === 404) {
    return;
  }

  const countryList = results;
  if (results.length > 10) {
    return error({
      text: 'To many matches found. Please enter a more specific query!',
    });
  }

  results.length > 1
    ? (countriesListRef.innerHTML = countriesListTemplate(countryList))
    : (countriesListRef.innerHTML = countryCardTemplate(countryList[0]));
}
